import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import About from '../views/About.vue'
import Contact from '../views/Contact.vue'
import Work from '../views/Work.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {title: "Dan's Portfolio" }
  },
  {
    path: "/about",
    name: "About",
    component: About,
    meta: { title: "About Me - Dan's Portfolio" }
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
    meta: { title: "Contact Me - Dan's Portfolio" }
  },
  {
    path: "/work",
    name: "Work",
    component: Work,
    meta: { title: "My Work - Dan's Portfolio" }
  }
];

const router = new VueRouter({
  mode: 'history',
  routes
});

router.afterEach((to, ) => {
  document.title = to.meta.title
})

export default router;
