<template>
  <div class="contact" >
    <Navbar bcolor="bg-black" tcolor="text-crayola" borcolor="border-crayola" />
    <div class="animate-in text-crayola flex h-screen">
      <h3 :class="submitted ? 'block' : 'hidden'" class="m-auto text-center font-quincy w-3/6 lg:w-1/2 text-3xl lg:text-4xl">
        Thank you! 🎉 I'll get back to you as soon as i can.
      </h3>
      <form
        ref="form"
        :v-model="form"
        @submit.prevent="onSubmit"
        :class="submitted ? 'hidden' : ''" 
        class="contact-form flex flex-col mx-auto pb-20 my-auto w-4/5 lg:w-1/2"
      >
        <h3 class="font-quincy text-4xl ">Send me a message! 📧</h3>
        <label class="font-quincy text-2xl">Name</label>
        <input v-model="form.name" class="mb-2 text-xl bg-black text-crayola border-2 border-crayola appearance-none rounded leading-tight focus:outline-none" type="text" name="name" required />
        <label class="font-quincy text-2xl">Email</label>
        <input v-model="form.email" class="mb-2 text-xl bg-black text-crayola border-2 border-crayola appearance-none rounded leading-tight focus:outline-none" type="email" name="email" required />
        <label class="font-quincy text-2xl">Message</label>
        <textarea v-model="form.msg" class="text-xl bg-black text-crayola border-2 border-crayola appearance-none rounded leading-tight focus:outline-none" name="message" required></textarea>
        <button type="submit" class="mt-6 text-2xl bg-crayola font-quincy hover:bg-crayolalight text-black font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">Send</button>
      </form>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Navbar from "@/components/Navbar.vue";
const API_URL = 'mail';

export default {
  name: "Contact",
  components: {
    Navbar
  },
  beforeCreate: function() {
    document.body.className = "bg-black";
  },
  data() {
    return {
      submitted: false,
      error: false,
      form: {
        name: '',
        email: '',
        msg: ''
      }
    }
  },
  methods: {
    onSubmit(){
      const name = this.form.name
      const email = this.form.email
      const msg = this.form.msg

      if (name.length > 0){
        const mail = {
          name,
          email,
          msg
        }
  
        fetch(API_URL, {
          method: 'POST',
          body: JSON.stringify(mail),
          headers: {
            'content-type': 'application/json'
          }
        });
        
        this.submitted = true;
      } else {
        console.log('Nope');
        
      }
      
    }
  }
};
</script>
